.item{
    display: grid;
    grid-template-columns: 1.5fr 10fr;
    grid-gap: 10px;
    margin-bottom: 15px;
}

.item .avatar{
    border: 1.5px solid #000000;
    height: 110px;
}

.item .text{
    font-size: 16px;
    border: 1.5px solid #000;
    padding: 15px;
    color: #000000;
}